.psv-markers {
  user-select: none;
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100%; }
  .psv-markers-svg-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 20; }

.psv-marker {
  display: none; }
  .psv-marker--normal {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 30;
    background-size: contain;
    background-repeat: no-repeat; }
  .psv-marker--transparent {
    display: block;
    opacity: 0; }
  .psv-marker--visible {
    display: block; }
  .psv-marker--has-tooltip, .psv-marker--has-content {
    cursor: pointer; }

.psv-markers-list {
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden; }
  .psv-markers-list-title {
    display: flex;
    align-items: center;
    font: 24px sans-serif;
    margin: 24px 0; }
    .psv-markers-list-title svg {
      width: 24px;
      height: 24px;
      margin: 0 12px; }
  .psv-markers-list-item {
    clear: both;
    min-height: 20px;
    padding: 0.5em 1em;
    cursor: pointer;
    transform: translateX(0);
    transition: transform .3s ease-in-out; }
    .psv-markers-list-item::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 10px;
      margin-left: -10px; }
    .psv-markers-list-item:nth-child(odd), .psv-markers-list-item:nth-child(odd)::before {
      background: rgba(255, 255, 255, 0.1); }
    .psv-markers-list-item:nth-child(even), .psv-markers-list-item:nth-child(even)::before {
      background: transparent; }
  .psv-markers-list-image {
    float: left;
    width: 20px; }
  .psv-markers-list .psv-button-svg * {
    fill: currentColor; }
  .psv-markers-list-name {
    margin: 0;
    padding: 0; }
  .psv-markers-list-image + .psv-markers-list-name {
    padding-left: calc(20px + 0.5em); }

.psv-container:not(.psv--is-touch) .psv-markers-list-item:hover {
  transform: translateX(10px);
  transition: transform .1s ease-in-out; }
